" 從事室內設計已30餘年的唐林建築室內設計總監廖韋強，身兼生活實踐家、極限運動家、大學講師等多種身分，熱愛廚藝、藝術繪畫與帆船運動，他從不為自己設限也不盲目追尋所謂的「主流」風尚，他以「狂」字定義自己，用自身觀點、信念，創造自己的語言，傳遞獨一無二的態度與精神。 ")]),_c('p',[_vm._v(" 他認為「瘋狂活著再死去，就是做設計的硬道理！」，笑著說：「我每次過了一個年度感覺就好像重生了。我把每年的作品都當作是最後的作品，做完了明年就不會出現第二次。」直率的話語如實反應他的個性與設計關鍵字──「變」，也就是不重複。無論是日常對話或是設計作品，他都希望有多樣化的呈現。規劃空間時亦設身處地換位思考，將自我意識提升到與委託人同樣的高度，模擬生活其中的情景、畫面、感受，反覆驗證、檢討，建構極具震撼力的居住體驗。希望能幫委託人把心裡面最原始的狀態、熱情及瘋狂的想法轉化為現實，讓他們推開門片彷彿走入哆啦A夢的任意門，進入到另一個世界，內心盈滿感動、快樂，開啟精采冒險。 ")]),_c('p',[_vm._v(" 未來，他希望能挑戰各種風格，也許是純淨無垢的「神之國度」，或是未來可能會蔚為風潮的亞洲風，冀望於各個作品中揉入深厚經驗值、對設計的熱情及創新思維，帶給委託人與自己刺激與愉悅，走出一條不平凡的道路。 ")])]),_c('div',{staticClass:"d-block d-md-none"},[_c('img',{attrs:{"src":require("@/assets/images/dsr/02/person_2.png"),"width":"100%","alt":"唐林建築室內設計"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"goright-btn-wrapper"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=681","target":"_blank"}},[_c('img',{staticClass:"goright-btn",attrs:{"src":require("@/assets/images/icon/goright.svg")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"intro03__portfolio"},[_c('div',{staticClass:"intro03__portfolio--lf"},[_vm._v("Portfolio")]),_c('div',{staticClass:"line2"}),_c('div',{staticClass:"intro03__portfolio--rt"},[_vm._v("MORE")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"intro03__portfolio--rt"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=681","target":"_blank"}},[_vm._v("SEE MORE")])])}]

