aticClass:"intro02__story--para"},[_vm._v(" 「身為一位設計師，有責任把業者的個人特質展現在設計作品中，為空間注入靈魂。」－林馬克 ")]),_c('p',[_vm._v(" 國際知名建築師林馬克，三十多年前因工來台，愛上台灣的美好，就此定居了下來，設計出知名的美麗華影城、華納威秀影城、新光三越百貨、錢櫃KTV等耳熟能詳的作品，創造許多雋永經典。他說：「在設計時，我不用電腦繪圖，而是用鉛筆細細描繪，我喜歡在設計中添入手工質感及人性溫度。」正因如此，他的作品都擁有獨一無二的靈魂，展現不完美的完美。 ")]),_c('p',[_vm._v(" 他自認，因為成長在3C電子產品不普遍的年代，偏愛純手工打造的物件，覺得缺陷也是一種美，就像知名車廠法拉利手工打造限量跑車、瑪莎拉蒂聘僱一百個人設計車子的聲音，都不是在追求機械化的功能，而是獨特的人性價值，這也使得它們無可取代，綻放迷人魅力。他將這個理念，在作品中發揮得淋漓盡致，像是北京極棧精品酒店作品，特別規劃了與旅客互動的燈光設計，使建築外觀依據每個房間點亮的燈光色彩變化，每天都不盡相同，充滿趣味性；為台灣藝術家林明宏所設計的住宅，亦是量身打造，且故意取消衛浴空間的門把，改成意思是「我要上廁所」的摩斯密碼標誌呈現，自然將幽默融入牆面，提升空間的故事性。這些別出心裁的巧思，貫徹著他所說的「身為一位設計師，有責任把業者的個人特質展現在設計作品中，為空間注入靈魂。」，使他的作品精彩豐富，傳遞濃濃的人情溫度。 ")])]),_c('div',{staticClass:"d-block d-md-none"},[_c('img',{attrs:{"src":require("@/assets/images/dsr/01/person_2.png"),"width":"100%","alt":"林馬克設計"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"goright-btn-wrapper"},[_c('a',{attrs:{"href":"https://hhh.com.tw/columns/detail/4977/","target":"_blank"}},[_c('img',{staticClass:"goright-btn",attrs:{"src":require("@/assets/images/icon/goright.svg")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"intro03__portfolio"},[_c('div',{staticClass:"intro03__portfolio--lf"},[_vm._v("Portfolio")]),_c('div',{staticClass:"line2"}),_c('div',{staticClass:"intro03__portfolio--rt"},[_vm._v("MORE")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"intro03__portfolio--rt"},[_c('a',{attrs:{"href":"https://hhh.com.tw/columns/detail/4977/","target":"_blank"}},[_vm._v("SEE MORE")])])}]

