計師的舞台，他笑說自己是站在歷史巨人的肩膀上，大膽想像並勾畫美好環境，將建築、記憶與文化融入設計，經由風格、元素襯托，加上尺度、機能的探討，讓使用者產生與空間、生活的對話，才是設計的本質及樂趣。 ")]),_c('p',[_vm._v(" 設計更依時勢形變，2020年人們因疫情隔離，只能透過網路維繫人際與經濟活動，使他更加篤定「居家辦公室」、「共享空間」必為未來趨勢，同時，兩者也互為配套，成為靈活運用空間的途徑。透過結合居家環境與辦公機能的彈性設計，依需求切換空間，如必要才到「共享空間」租借會議室進行面對面的討論，因此，「共享空間」就必須分散動線、避免交叉感染，空調系統亦加強過濾、換氣，甚至設立體溫測量點、臨時等待隔離的負壓空間等重點，都將影響未來空間的規劃與設計。 ")]),_c('p',[_vm._v(" 透過宏觀視野，方志宏總監剖析設計未來面臨的變化，亦期待他繼續以獨特角度，帶來更多嶄新觀點與優秀作品。 ")])]),_c('div',{staticClass:"d-block d-md-none"},[_c('img',{attrs:{"src":require("@/assets/images/dsr/04/person_2.png"),"width":"100%","alt":"麒成益設計"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"goright-btn-wrapper"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=701","target":"_blank"}},[_c('img',{staticClass:"goright-btn",attrs:{"src":require("@/assets/images/icon/goright.svg")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"intro03__portfolio"},[_c('div',{staticClass:"intro03__portfolio--lf"},[_vm._v("Portfolio")]),_c('div',{staticClass:"line2"}),_c('div',{staticClass:"intro03__portfolio--rt"},[_vm._v("MORE")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"intro03__portfolio--rt"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=507","target":"_blank"}},[_vm._v("SEE MORE")])])}]

