細膩地為台灣創建更好的設計空間。 ")]),_c('p',[_vm._v(" 從小旅居加拿大，畢業於加拿大曼尼托巴大學建築系的陳琬婷總監，與藝術淵源極深，求學路上在師長的啟發下，讓她對設計產生憧憬，就此啟蒙了設計創作生涯，走上建築與室內設計這條路！「在國外的求學環境，接觸到許多不同的人事物，遊歷各國的所見所聞，都是我做設計的養分」，談到設計，雙眼總透著濃烈的熱情，而這正是她之所以被稱為「設計狂小姐」的原因。堅持「莫忘初衷」，推著她專注地投入眼前每一件事情，不僅讓她獲得客戶的認同與喜愛，近年來，設計作品也一直在國際舞台上發光發熱。 ")]),_c('p',[_vm._v(" 擅於整合機能與美學設計，讓設計及工藝層次與先進跳脫框架，並關注環境綠建材議題與應用，她時常透過服務項目與業主倡議環保建材及環境保護的重要。2020疫情隔離為人們的生活帶來不小的影響，她認為，極簡素淨且安靜高雅的居宅，屏除繁複線條，進駐綠意的舒心安全居所，將成為設計的重心。身為設計前端者的角色，她堅持為台灣及環境美學設計盡一份力量，放眼國際，回饋客戶與社會，賦予室內建築的使用空間更多的美意。 ")])]),_c('div',{staticClass:"d-block d-md-none"},[_c('img',{attrs:{"src":require("@/assets/images/dsr/03/person_2.png"),"width":"100%","alt":"舒杰室內裝修設計"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"goright-btn-wrapper"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=507","target":"_blank"}},[_c('img',{staticClass:"goright-btn",attrs:{"src":require("@/assets/images/icon/goright.svg")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"intro03__portfolio"},[_c('div',{staticClass:"intro03__portfolio--lf"},[_vm._v("Portfolio")]),_c('div',{staticClass:"line2"}),_c('div',{staticClass:"intro03__portfolio--rt"},[_vm._v("MORE")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"intro03__portfolio--rt"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=507","target":"_blank"}},[_vm._v("SEE MORE")])])}]

