期待的事，居住空間被視為延伸居住者生活品味的最佳場景，雲方室內設計潘仕敏設計總監透過擅長的古典風格設計，本著協助每一位業主打造專屬質感居家為理念，以細膩的古典設計語彙，賦予每個作品表述溫馨與精緻度的居住氛圍。 ")]),_c('p',[_vm._v(" 「古典風格饒富多樣性，更是經典不敗的風格之選」，這是潘仕敏設計總監鍾情古典風格的原因，在細膩的古典元素鋪敘下，具體傳遞了古典美學在現代社會的多元風采，讓空間襯托居住者之於生活豐厚的涵養與品味，且充滿情感溫度。過往常利用出國參訪古典風飯店、餐廳的所見所聞轉化為她在設計時的養分，讓每個作品都能更道地展現獨有風情。每次與業主的合作，她也特別講究前期充分的溝通，注重細節，精於搭配，嚴謹工程把關，將每一個細節做到完美，而這正是她對設計的堅持。 ")]),_c('p',[_vm._v(" 擅於從英式、美式、鄉村等各類不同古典風格之中，帶領業主梳理出自己鍾愛的風格屬性，再以詳實平面配置規劃為基礎，佐以軟裝色彩佈置、五金配件、燈飾等，恰到好處地融合應用。「帶領大家跳脫一般人對於古典風格老氣、拘謹的刻板印象，而我們要做的就是透過布料、油漆、壁紙、五金的靈活運用，勾勒休憩舒適性」，在空間佈局、設計手法、精裝風格細節上加以琢磨，為業主營造道地且因地制宜的風格居家，圍塑舒適迷人的生活場景，砌築業主所嚮往的居家樣貌。 ")])]),_c('div',{staticClass:"d-block d-md-none"},[_c('img',{attrs:{"src":require("@/assets/images/dsr/05/person_2.png"),"width":"100%","alt":"雲方設計"}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"goright-btn-wrapper"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=182","target":"_blank"}},[_c('img',{staticClass:"goright-btn",attrs:{"src":require("@/assets/images/icon/goright.svg")}})])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"d-none d-md-block"},[_c('div',{staticClass:"intro03__portfolio"},[_c('div',{staticClass:"intro03__portfolio--lf"},[_vm._v("Portfolio")]),_c('div',{staticClass:"line2"}),_c('div',{staticClass:"intro03__portfolio--rt"},[_vm._v("MORE")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"intro03__portfolio--rt"},[_c('a',{attrs:{"href":"https://hhh.com.tw/designer-index.php?designer_id=507","target":"_blank"}},[_vm._v("SEE MORE")])])}]

